const Crear = () => {
    const { t } = useTranslation();
    const theme = useTheme();
    const classes = useStyles();
    const token = window.localStorage.getItem('accessToken');
    const { enqueueSnackbar } = useSnackbar();

    const [cargar,setCargar] = useState(false)
    const [actualizar,setActualizar] = useState(false)
    const [open, setOpen] = useState(false);
    const [nombreRubro,setNombreRubro] = useState('')
    const [checked, setChecked] = useState([true, false]);
    const [rubros,setRubros] = useState(null)
    const [eliminarId,setEliminarId] = useState(null)
    const [openConfirmDelete, setOpenConfirmDelete] = useState(false);
    const [value,setValue] = useState('')
    const [titulo,setTitulo] = useState('')
    const [correo,setCorreo] = useState('')
    const [web,setWeb] = useState('')
    const [telefono,setTelefono] = useState('')
    const [imagen,setImagen] = useState()
    const [checkedItems, setCheckedItems] = useState(new Map());
    const [file, setFile] = useState([]);




    const handleChangeCheck = (event) => {
      setCheckedItems(checkedItems => checkedItems.set(event.target.name, event.target.checked));
      console.log("checkedItems: ", checkedItems);
  }


    const llamadaRubro = async() => {

      const result =  await getCategorias(token)
      setRubros(result)

    }


   useEffect(() => {
      
      llamadaRubro()

      setFile('https://i.picsum.photos/id/1005/600/200.jpg?hmac=nSUIMtCUdLn9MDeLlcBLYAQqx6QA5SbJkKrjYp_HhL4')

    }, [])

    useEffect(() => {
      
      llamadaRubro()
      setActualizar(false)

    }, [actualizar])


    const handleChange1 = (event) => {
        setChecked([event.target.checked, event.target.checked]);
      };
    
      const handleChange2 = (event) => {
        setChecked([event.target.checked, checked[1]]);
      };
    
      const handleChange3 = (event) => {
        setChecked([checked[0], event.target.checked]);
      };
    
      const [productTags, setProductTags] = useState([
        { key: 0, label: 'new' },
        { key: 1, label: 'fresh' },
        { key: 2, label: 'electronics' },
        { key: 3, label: 'computer' },
        { key: 4, label: 'software' }
      ]);
    
      const handleDelete = (productTagToDelete) => () => {
        setProductTags((productTags) =>
          productTags.filter(
            (productTag) => productTag.key !== productTagToDelete.key
          )
        );
      };

  const handleOpen = () => {
      setOpen(true);
    };
  
    const handleClose = () => {
      setOpen(false);
    };




      const crear = async() => {
        handleClose()

        let obj = {
          nombre: nombreRubro
        }

     const result =  await crearCategoria(obj,token)
        

     if(result.message){

      setActualizar(true)
      
      enqueueSnackbar(result.message, {
        variant: 'success',
        anchorOrigin: {
          vertical: 'top',
          horizontal: 'right'
        }
      });

     }
     else{
      enqueueSnackbar(result.error, {
        variant: 'error',
        anchorOrigin: {
          vertical: 'top',
          horizontal: 'right'
        }
      });
     }

      }
    
      const children = (
        <Box sx={{ display: 'flex', flexDirection: 'column', ml: 3 }}>
          <FormControlLabel
            label="Appliances"
            control={<Checkbox checked={checked[0]} onChange={handleChange2} />}
          />
          <FormControlLabel
            label="Outdoor equipment"
            control={<Checkbox checked={checked[1]} onChange={handleChange3} />}
          />
        
        </Box>
      );

      
    const {
        acceptedFiles,
        isDragActive,
        isDragAccept,
        isDragReject,
       getRootProps,
        getInputProps
      } = useDropzone({
        accept: 'image/jpeg, image/png',
        onDrop: acceptedFiles => {
          setFile(acceptedFiles.map(file => Object.assign(file, {
            preview: URL.createObjectURL(file)
          })));
      
        }
      });

      const files = acceptedFiles.map((file, index) => (
        <ListItem disableGutters component="div" key={index}>
          <ListItemText primary={file.name} />
          <b>{file.size} bytes</b>
          <Divider />
          <img
          src={file.preview}
          className={classes.previewImg}
        />
        </ListItem>
      ));

    

      const handleConfirmDelete = (param) => {

        setEliminarId(param)
        setOpenConfirmDelete(true);
      };
    
      const closeConfirmDelete = () => {
        setOpenConfirmDelete(false);
      };
    
      const handleDeleteCompleted = async () => {
        setOpenConfirmDelete(false);
    
        

      const result =  await eliminarCategoria(eliminarId,token)


      if(result.message){


        setActualizar(true)
        enqueueSnackbar(result.message, {
          variant: 'success',
          anchorOrigin: {
            vertical: 'top',
            horizontal: 'right'
          }
        });

      }else{

        enqueueSnackbar(result.error, {
          variant: 'error',
          anchorOrigin: {
            vertical: 'top',
            horizontal: 'right'
          }
        });

      }

    
      
      };
    
    
      console.log('eliminar',eliminarId)

      const updateImagen = (e) => {


        setImagen(e.target.files[0])

      }


      const publicar = async() => {

        setCargar(true)

        let arr = []
        for(let val of checkedItems){
          console.log('val',val)
          arr.push(Number(val[0]))
        }

        console.log('arrfinal',arr)

        let obj = {
          titulo: titulo,
          descripcion: value,
          categoria: arr
        }



        var myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("Authorization", "Bearer " + token)

        var formdata = new FormData();
        formdata.set("imagen",acceptedFiles[0]);

        let parseVal = JSON.stringify(obj)
                  
        formdata.set("data",parseVal);
                  
                  var requestOptions = {
                    method: 'POST',
                    headers: myHeaders,
                    body: formdata,
                    redirect: 'follow'
                  };


                  const result = await fetch("https://api.klubo.club/api/superadmin/info", requestOptions)
                  const resultFinal = await result.json()
                   console.log(resultFinal)

                   setCargar(false)

                   if(resultFinal.message){

                    setActualizar(true)

                    setTelefono('')
                    setTitulo('')
                    setCorreo('')
                    setWeb('')
                    setValue('')
                    setImagen('')
                    setCheckedItems(new Map())
                    
                    enqueueSnackbar(resultFinal.message, {
                      variant: 'success',
                      anchorOrigin: {
                        vertical: 'top',
                        horizontal: 'right'
                      }
                    });
              
                   }
                   else{
                    enqueueSnackbar(resultFinal.error, {
                      variant: 'error',
                      anchorOrigin: {
                        vertical: 'top',
                        horizontal: 'right'
                      }
                    });
                   }
  

       }

      console.log("checkedItems: ", checkedItems);

      console.log('get input',acceptedFiles[0])


    return ( 
        <>
   <DialogWrapper
        open={openConfirmDelete}
        maxWidth="sm"
        fullWidth
        TransitionComponent={Transition}
        keepMounted
        onClose={closeConfirmDelete}
      >
        <Box
          display="flex"
          alignItems="center"
          justifyContent="center"
          flexDirection="column"
          p={5}
        >
          <AvatarError>
            <CloseIcon />
          </AvatarError>

          <Typography align="center" sx={{ py: 4, px: 6 }} variant="h2">
            {t('Est seguro de que desea eliminar permanentemente este deporte')}
            ?
          </Typography>

          <Box>
            <Button
              variant="text"
              size="large"
              sx={{ mx: 1 }}
              onClick={closeConfirmDelete}
            >
              {t('Cancelar')}
            </Button>
            <ButtonError
              onClick={handleDeleteCompleted}
              size="large"
              sx={{ mx: 1, px: 3 }}
              variant="contained"
            >
              {t('Eliminar')}
            </ButtonError>
          </Box>
        </Box>
      </DialogWrapper>


                    <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paperModal}>

           <TextField
          fullWidth
          id="outlined-required"
          label="Agregar Categoria"
          placeholder="Agregar..."
          variant="outlined"
          onChange={(e) => setNombreRubro(e.target.value)}
        />
       <Grid xs={12} display="flex"  justifyContent="flex-end" mt={3}>
       <Button variant="contained" color="primary" 
       onClick={crear}
       >
   Agregar Categoria
</Button>
<Button variant="outlined" color="secondary"  className={classes.cancelar} onClick={handleClose}>
  Cancelar
</Button>

       </Grid>

          </div>
        </Fade>
      </Modal>
            <ContentWrapper title="Crear beneficios">
      <Container maxWidth="lg"             className="field"
>

       
        
        <PageHeader/>
    <Grid xs={12} display="flex">
    <Grid  xs={8.8} >
    <Grid xs={12} mt={3} pr={3}>
  


    <Card sx={{ p: 3 }}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <TextField
            fullWidth
            
            onChange={(e)=>setTitulo(e.target.value)}
            placeholder={t('Titulo')}
            variant="outlined"
          />
        </Grid>
       
       
       
        <Grid item xs={12}>
          <EditorWrapper>
            <ReactQuill 
              value={value} onChange={setValue} 
            />
          </EditorWrapper>
        </Grid>
      
      </Grid>
    </Card>

  
    </Grid>
    <Grid xs={12} mt={3} pr={3} mb={5}>
    <Card>
        <CardHeader title={t('Product Images')} />
        <Divider />
        <Box p={2}>
          <BoxUploadWrapper {...getRootProps()}>
            <input {...getInputProps()} 
               
            />
            {isDragAccept && (
              <>
                <AvatarSuccess variant="rounded">
                  <CheckTwoToneIcon />
                </AvatarSuccess>
                <Typography sx={{ mt: 2 }}>
                  {t('Drop the files to start uploading')}
                </Typography>
              </>
            )}
            {isDragReject && (
              <>
                <AvatarDanger variant="rounded">
                  <CloseTwoToneIcon />
                </AvatarDanger>
                <Typography sx={{ mt: 2 }}>
                  {t('You cannot upload these file types')}
                </Typography>
              </>
            )}
            {!isDragActive && (
              <>
                <AvatarWrapper variant="rounded">
                  <CloudUploadTwoToneIcon />
                </AvatarWrapper>
                <Typography sx={{ mt: 2 }}>
                  {t('Drag & drop files here')}
                </Typography>
              </>
            )}
 

          </BoxUploadWrapper>
        </Box>

        {files.length > 0 && (
          <>
            <Divider />
            <Box p={2}>
              <Alert sx={{ py: 0 }} severity="success">
                {t('You have uploaded')} <b>{files.length}</b> {t('files')}!
              </Alert>
              <List disablePadding sx={{ mt: 2 }} component="div">
                {files}
              </List>
            </Box>
          </>
        )}
      </Card>
    </Grid>
  </Grid>
  <Grid  xs={3.2}  >
   
    <Grid xs={12} mt={3} >
    <Card>
 
        
      
      
        <Box p={3}>
          <Grid container spacing={2}>
           
            <Grid item xs={12}>
              <Button  style={{'height': '40px'}}  fullWidth variant="contained" onClick={()=>publicar()}
                            disabled={cargar === true ? true : false}

              >

{cargar=== true ? 
            
            <CircularProgress color="primary"  />

              :
              'Publicar'

              }
                
              </Button>
            </Grid>
          </Grid>
        </Box>
      </Card>
    
    </Grid>
    <Grid xs={12} mt={3} mb={3}>
    <Card>
        <CardHeader
          action={
            <Button size="small" variant="outlined"
            
            onClick={handleOpen}
            >
              {t('Agregar categoria')}
            </Button>
          }
          title={t('Categorias')}
        />
        <Divider />
       
            
      


          {rubros !== null && rubros.map((val)=>(
            <>
             <Grid 
               container
               
               direction="row"
               justifyContent="start"
               alignItems="center"
               pl={3}
             
             >
          <FormControlLabel control={<Checkbox name={val.id} checked={checkedItems.get(val.id)} onChange={handleChangeCheck} />} label={val.nombre} />


         
          <DeleteTwoToneIcon  onClick={()=> handleConfirmDelete(val.id)} className={classes.eliminar} />
          </Grid>
          </>
          ))}


      
       
      </Card>

    </Grid>
    
    </Grid>
    </Grid>

    </Container>
    </ContentWrapper>
        </>
     );
}
 
export default Crear;